
html, body, #app {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
#app {
  border: 1px solid #d7dde4;
  background: #f5f7f9;
  position: relative;
}
.layout-breadcrumb {
  padding: 10px 15px 0;
}
.layout-content {
  min-height: calc(100vh - 111px);
  margin: 15px;
  overflow: hidden;
  background: #fff;
  border-radius: 4px;
}
.layout-content-main {
  padding: 10px;
}
.layout-copy {
  text-align: center;
  padding: 10px 0 20px;
  color: #9ea7b4;
}
.layout-menu-left {
  background: #464c5b;
}
.layout-logo-left {
  width: 90%;
  height: 30px;
  background: #5b6270;
  border-radius: 3px;
  margin: 15px auto;
}


















































































































































































































































































































































































































































































































































































































































































































































































































#e32 rect {
  -webkit-transition: all .3s;
  transition: all .3s;
  fill: steelblue;
}
#e32 rect:hover {
  fill: #0000ff;
}
#e32 text {
  fill: #000;
  font: 10px sans-serif;
  text-anchor: middle;
}
#e32 .axis text {
  font: 10px sans-serif;
  fill: #000;
  text-anchor: end;
}
.axis path,
.axis line {
  fill: none;
  stroke: #000;
  shape-rendering: crispEdges;
}
#e321 text {
  font: bold 48px monospace;
}
#e321 .enter {
  fill: green;
}
#e321 .update {
  fill: #333;
}
#e321 .exit {
  fill: brown;
}
circle.point {
  fill: #fff;
  stroke: #000;
  -webkit-transition: all .2s;
  transition: all .2s;
  cursor: pointer;
}
circle.point:hover {
  stroke-width: 3px;
}

.businessEffectSvgWrapper-e33 {
  position: relative;
}
.businessEffectSvgWrapper-e33 .businessEffectSvg {
  position: absolute;
  left: 0;
  top: 0;
  cursor: move;
}
.businessEffectSvgWrapper-e33 .businessEffectSvg image.asset {
  cursor: pointer;
}
.businessEffectSvgWrapper-e33 .businessEffectSvg text {
  cursor: text;
}

.businessEffectSvgWrapper {
  position: relative;
}
.businessEffectSvg {
  position: absolute;
  left: 0;
  top: 0;
  cursor: move;
}
.businessEffectSvg line.swimLine {
  stroke: #4ce9ff;
}
.businessEffectSvg line.rel {
  stroke: #3333ff;
  stroke-dasharray: 5, 5;
}
.businessEffectSvg image.asset {
  cursor: pointer;
}
.businessEffectSvg text {
  cursor: text;
  fill: #6f75ff;
}
